import request from '@/utils/request'
import type { UserDetail, Membership } from '@/types/user'

// 更新当前用户
export async function updateUser(data: {
  nickname?: string
  avatar?: string
  password_old?: string
  password_new?: string
}): Promise<UserDetail> {
  await request.patch('/users/me', data)
  const res = await request.get<Record<string, unknown>>('/users/me')
  const d = res.data
  const membership = d.membership as
    | { plan: string; expired_at: string; status: string }
    | undefined
  return {
    id: d.id as number,
    email: d.email as string,
    phone: d.phone as string | undefined,
    nickname: (d.nickname as string) || '',
    avatar: d.avatar as string | undefined,
    role: d.role as UserDetail['role'],
    status: d.status as UserDetail['status'],
    created_at: (d.created_at as string) ?? '',
    updated_at: (d.updated_at as string) ?? '',
    ...(membership && {
      membership: {
        id: 0,
        user_id: d.id as number,
        plan: membership.plan as 'student' | 'teacher',
        started_at: '',
        expired_at: membership.expired_at,
        status: membership.status as 'active' | 'expired' | 'cancelled',
        created_at: '',
        updated_at: '',
      },
    }),
  }
}

// 获取当前会员信息
export async function getMembership(): Promise<Membership | null> {
  const res = await request.get<Membership | null>('/memberships/me')
  return res.data
}

// 购买/续费会员
export async function purchaseMembership(plan: 'student' | 'teacher'): Promise<Membership> {
  const res = await request.post<Membership>('/memberships/purchase', { plan })
  return res.data
}

// 注销账号（需密码确认）
export async function cancelAccount(password: string): Promise<void> {
  await request.post('/users/me/cancel', { password })
}
