<template>
  <div class="profile-page">
    <div class="container">
      <el-row :gutter="20">
        <el-col :xs="24" :sm="24" :md="6" :lg="6">
          <el-card shadow="never" class="profile-card">
            <div class="avatar-section">
              <el-avatar :size="100" :src="form.avatar">
                {{ form.nickname?.[0] }}
              </el-avatar>
              <h3 class="header-font">{{ form.nickname }}</h3>
              <p class="email">{{ form.email }}</p>
              <el-tag v-if="authStore.isMember" type="success">会员</el-tag>
              <el-tag v-else type="info">普通用户</el-tag>
            </div>
          </el-card>
        </el-col>
        <el-col :xs="24" :sm="24" :md="18" :lg="18">
          <el-tabs v-model="activeTab" class="profile-tabs">
            <el-tab-pane label="个人信息" name="info">
              <el-card shadow="never">
                <el-form
                  :model="form"
                  :rules="rules"
                  ref="formRef"
                  class="profile-form"
                  label-width="100px"
                >
                  <el-form-item label="昵称" prop="nickname">
                    <el-input v-model="form.nickname" />
                  </el-form-item>
                  <el-form-item label="邮箱">
                    <el-input v-model="form.email" disabled />
                  </el-form-item>
                  <el-form-item label="手机号">
                    <el-input v-model="form.phone" disabled />
                  </el-form-item>
                  <el-form-item>
                    <el-button type="primary" @click="handleUpdateInfo">保存</el-button>
                  </el-form-item>
                </el-form>
              </el-card>
            </el-tab-pane>

            <el-tab-pane label="修改密码" name="password">
              <el-card shadow="never">
                <el-form
                  :model="passwordForm"
                  :rules="passwordRules"
                  ref="passwordFormRef"
                  class="profile-form"
                  label-width="100px"
                >
                  <el-form-item label="原密码" prop="oldPassword">
                    <el-input v-model="passwordForm.oldPassword" type="password" show-password />
                  </el-form-item>
                  <el-form-item label="新密码" prop="newPassword">
                    <el-input v-model="passwordForm.newPassword" type="password" show-password />
                  </el-form-item>
                  <el-form-item label="确认密码" prop="confirmPassword">
                    <el-input
                      v-model="passwordForm.confirmPassword"
                      type="password"
                      show-password
                    />
                  </el-form-item>
                  <el-form-item>
                    <el-button type="primary" @click="handleUpdatePassword">修改密码</el-button>
                  </el-form-item>
                </el-form>
              </el-card>
            </el-tab-pane>

            <el-tab-pane label="会员信息" name="membership">
              <el-card shadow="never">
                <div v-if="membership" class="membership-info">
                  <el-descriptions :column="1" border>
                    <el-descriptions-item label="会员计划">{{
                      membership.plan
                    }}</el-descriptions-item>
                    <el-descriptions-item label="状态">
                      <el-tag :type="membership.status === 'active' ? 'success' : 'info'">
                        {{ membership.status === 'active' ? '有效' : '已过期' }}
                      </el-tag>
                    </el-descriptions-item>
                    <el-descriptions-item label="开始时间">
                      {{ new Date(membership.started_at).toLocaleDateString() }}
                    </el-descriptions-item>
                    <el-descriptions-item label="到期时间">
                      {{ new Date(membership.expired_at).toLocaleDateString() }}
                    </el-descriptions-item>
                  </el-descriptions>
                  <div class="membership-actions">
                    <el-button
                      v-if="!authStore.isMember"
                      type="primary"
                      @click="handlePurchaseMembership"
                    >
                      升级会员
                    </el-button>
                  </div>
                </div>
                <el-empty v-else description="您还不是会员">
                  <el-button type="primary" @click="handlePurchaseMembership">购买会员</el-button>
                </el-empty>
              </el-card>
            </el-tab-pane>

            <el-tab-pane label="收藏夹" name="favorites">
              <el-card shadow="never">
                <div v-if="favorites.length || favoritesLoading" class="table-scroll-wrap">
                  <el-table
                    v-loading="favoritesLoading"
                    :data="favorites"
                    class="profile-table"
                    style="width: 100%"
                  >
                    <el-table-column prop="title" label="标题" min-width="120">
                      <template #default="{ row }">
                        <router-link :to="`/essay/${row.essay_id}`" class="table-link">
                          {{ row.title || '无标题' }}
                        </router-link>
                      </template>
                    </el-table-column>
                    <el-table-column label="收藏时间" min-width="150">
                      <template #default="{ row }">
                        {{ new Date(row.essay_created_at).toLocaleString() }}
                      </template>
                    </el-table-column>
                    <el-table-column label="操作" min-width="100" align="right">
                      <template #default="{ row }">
                        <el-button
                          type="danger"
                          text
                          :loading="unfavoriteId === row.essay_id"
                          @click="handleUnfavorite(row)"
                        >
                          取消收藏
                        </el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                </div>
                <el-empty v-else description="暂无收藏" />
              </el-card>
            </el-tab-pane>

            <el-tab-pane label="素材收藏" name="classicFavorites">
              <el-card shadow="never">
                <div
                  v-if="classicFavorites.length || classicFavoritesLoading"
                  class="table-scroll-wrap"
                >
                  <el-table
                    v-loading="classicFavoritesLoading"
                    :data="classicFavorites"
                    class="profile-table"
                    style="width: 100%"
                  >
                    <el-table-column label="内容" min-width="180">
                      <template #default="{ row }">
                        <router-link :to="`/classic/item/${row.id}`" class="table-link">
                          {{ row.original_text?.slice(0, 50)
                          }}{{ (row.original_text?.length ?? 0) > 50 ? '…' : '' }}
                        </router-link>
                      </template>
                    </el-table-column>
                    <el-table-column label="作者/朝代" min-width="100">
                      <template #default="{ row }"> {{ row.author }} {{ row.dynasty }} </template>
                    </el-table-column>
                    <el-table-column label="操作" min-width="100" align="right">
                      <template #default="{ row }">
                        <el-button
                          type="danger"
                          text
                          :loading="unfavoriteClassicId === row.id"
                          @click="handleUnfavoriteClassic(row)"
                        >
                          取消收藏
                        </el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                </div>
                <el-empty v-else description="暂无素材收藏">
                  <el-button type="primary" @click="$router.push('/classic')">去素材中心</el-button>
                </el-empty>
              </el-card>
            </el-tab-pane>

            <el-tab-pane label="反馈建议" name="feedback">
              <el-card shadow="never">
                <el-form :model="feedbackForm" class="profile-form" label-width="100px">
                  <el-form-item label="反馈内容">
                    <el-input
                      v-model="feedbackForm.content"
                      type="textarea"
                      :rows="6"
                      placeholder="请输入您的反馈或建议..."
                    />
                  </el-form-item>
                  <el-form-item label="联系方式">
                    <el-input v-model="feedbackForm.contact" placeholder="邮箱或手机号（可选）" />
                  </el-form-item>
                  <el-form-item>
                    <el-button type="primary" @click="handleSubmitFeedback">提交反馈</el-button>
                  </el-form-item>
                </el-form>
              </el-card>
            </el-tab-pane>

            <el-tab-pane label="账号安全" name="security">
              <el-card shadow="never">
                <div class="security-section">
                  <h4>注销账号</h4>
                  <p class="security-desc">注销后账号及关联数据将被删除且无法恢复，请谨慎操作。</p>
                  <el-button type="danger" plain @click="openCancelDialog">申请注销账号</el-button>
                </div>
              </el-card>
              <el-dialog
                v-model="cancelDialogVisible"
                title="注销账号"
                width="400px"
                class="cancel-account-dialog"
                :close-on-click-modal="false"
                @closed="cancelForm.password = ''"
              >
                <el-alert
                  class="tip-alert cancel-dialog-tip"
                  title="提示"
                  type="warning"
                  :closable="false"
                  show-icon
                >
                  注销后账号及关联数据将被删除且无法恢复，请谨慎操作。确认无误后请输入当前密码并点击「确认注销」。
                </el-alert>
                <el-form
                  :model="cancelForm"
                  label-width="80px"
                  @submit.prevent="handleCancelAccount"
                >
                  <el-form-item label="当前密码" required>
                    <el-input
                      v-model="cancelForm.password"
                      type="password"
                      show-password
                      placeholder="请输入当前密码以确认"
                      @keyup.enter="handleCancelAccount"
                    />
                  </el-form-item>
                </el-form>
                <template #footer>
                  <el-button @click="cancelDialogVisible = false">取消</el-button>
                  <el-button type="danger" :loading="cancelLoading" @click="handleCancelAccount">
                    确认注销
                  </el-button>
                </template>
              </el-dialog>
            </el-tab-pane>
          </el-tabs>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted, watch } from 'vue'
import { useRouter } from 'vue-router'
import { useAuthStore } from '@/stores/auth'
import * as userApi from '@/api/user'
import * as feedbackApi from '@/api/feedback'
import * as essayApi from '@/api/essay'
import * as classicApi from '@/api/classic'
import { ElMessage, type FormInstance, type FormRules } from 'element-plus'
import type { Membership } from '@/types/user'
import type { FavoriteItem } from '@/types/essay'
import type { ClassicItem } from '@/types/classic'

const router = useRouter()
const authStore = useAuthStore()

const activeTab = ref('info')
const formRef = ref<FormInstance>()
const passwordFormRef = ref<FormInstance>()
const membership = ref<Membership | null>(null)

const form = reactive({
  nickname: '',
  email: '',
  phone: '',
  avatar: '',
})

const passwordForm = reactive({
  oldPassword: '',
  newPassword: '',
  confirmPassword: '',
})

const feedbackForm = reactive({
  content: '',
  contact: '',
})

const cancelDialogVisible = ref(false)
const cancelLoading = ref(false)
const cancelForm = reactive({ password: '' })

const favorites = ref<FavoriteItem[]>([])
const favoritesLoading = ref(false)
const unfavoriteId = ref<number | null>(null)
const classicFavorites = ref<ClassicItem[]>([])
const classicFavoritesLoading = ref(false)
const unfavoriteClassicId = ref<number | null>(null)

const rules: FormRules = {
  nickname: [
    { required: true, message: '请输入昵称', trigger: 'blur' },
    { min: 2, max: 20, message: '昵称长度在2到20个字符', trigger: 'blur' },
  ],
}

const validateConfirmPassword = (
  _rule: unknown,
  value: string,
  callback: (error?: Error) => void
) => {
  if (value !== passwordForm.newPassword) {
    callback(new Error('两次输入的密码不一致'))
  } else {
    callback()
  }
}

const passwordRules: FormRules = {
  oldPassword: [{ required: true, message: '请输入原密码', trigger: 'blur' }],
  newPassword: [
    { required: true, message: '请输入新密码', trigger: 'blur' },
    { min: 6, message: '密码长度不能少于6位', trigger: 'blur' },
  ],
  confirmPassword: [
    { required: true, message: '请确认密码', trigger: 'blur' },
    { validator: validateConfirmPassword, trigger: 'blur' },
  ],
}

onMounted(async () => {
  if (authStore.user) {
    form.nickname = authStore.user.nickname
    form.email = authStore.user.email || ''
    form.phone = authStore.user.phone || ''
    form.avatar = authStore.user.avatar || ''
  }
  await fetchMembership()
})

watch(activeTab, (tab) => {
  if (tab === 'favorites') fetchFavorites()
  if (tab === 'classicFavorites') fetchClassicFavorites()
})

async function fetchFavorites() {
  favoritesLoading.value = true
  try {
    favorites.value = await essayApi.getFavorites()
  } catch {
    favorites.value = []
  } finally {
    favoritesLoading.value = false
  }
}

async function handleUnfavorite(item: FavoriteItem) {
  unfavoriteId.value = item.essay_id
  try {
    await essayApi.unfavoriteEssay(item.essay_id)
    favorites.value = favorites.value.filter((f) => f.essay_id !== item.essay_id)
    ElMessage.success('已取消收藏')
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '取消收藏失败')
  } finally {
    unfavoriteId.value = null
  }
}

async function fetchClassicFavorites() {
  classicFavoritesLoading.value = true
  try {
    classicFavorites.value = await classicApi.getClassicFavorites()
  } catch {
    classicFavorites.value = []
  } finally {
    classicFavoritesLoading.value = false
  }
}

async function handleUnfavoriteClassic(c: ClassicItem) {
  unfavoriteClassicId.value = c.id
  try {
    await classicApi.unfavorite(c.id)
    classicFavorites.value = classicFavorites.value.filter((x) => x.id !== c.id)
    ElMessage.success('已取消收藏')
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '取消收藏失败')
  } finally {
    unfavoriteClassicId.value = null
  }
}

async function fetchMembership() {
  try {
    membership.value = await userApi.getMembership()
  } catch {
    // 忽略错误
  }
}

async function handleUpdateInfo() {
  if (!formRef.value) return
  await formRef.value.validate(async (valid) => {
    if (valid) {
      try {
        await userApi.updateUser({ nickname: form.nickname })
        await authStore.fetchUser()
        ElMessage.success('更新成功')
      } catch (error: unknown) {
        ElMessage.error((error as Error).message || '更新失败')
      }
    }
  })
}

async function handleUpdatePassword() {
  if (!passwordFormRef.value) return
  await passwordFormRef.value.validate(async (valid) => {
    if (valid) {
      try {
        await userApi.updateUser({
          password_old: passwordForm.oldPassword,
          password_new: passwordForm.newPassword,
        })
        ElMessage.success('密码修改成功')
        passwordForm.oldPassword = ''
        passwordForm.newPassword = ''
        passwordForm.confirmPassword = ''
      } catch (error: unknown) {
        ElMessage.error((error as Error).message || '密码修改失败')
      }
    }
  })
}

async function handlePurchaseMembership() {
  try {
    const result = await userApi.purchaseMembership('student')
    membership.value = result
    await authStore.fetchUser()
    ElMessage.success('会员购买成功')
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '购买失败')
  }
}

async function handleSubmitFeedback() {
  if (!feedbackForm.content.trim()) {
    ElMessage.warning('请输入反馈内容')
    return
  }
  try {
    await feedbackApi.createFeedback({
      content: feedbackForm.content.trim(),
      contact: feedbackForm.contact.trim() || undefined,
    })
    ElMessage.success('反馈提交成功，感谢您的建议！')
    feedbackForm.content = ''
    feedbackForm.contact = ''
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '提交失败')
  }
}

function openCancelDialog() {
  cancelForm.password = ''
  cancelDialogVisible.value = true
}

async function handleCancelAccount() {
  if (!cancelForm.password.trim()) {
    ElMessage.warning('请输入当前密码')
    return
  }
  cancelLoading.value = true
  try {
    await userApi.cancelAccount(cancelForm.password)
    authStore.clearLocal()
    cancelDialogVisible.value = false
    ElMessage.success('账号已注销')
    router.replace('/')
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '注销失败')
  } finally {
    cancelLoading.value = false
  }
}
</script>

